import "./styles.css";
import {
  getweatherData,
  extractData,
  getDeviceLocationWeatherData,
  renderWeatherData,
  fetchData
} from "./helpers";

// console.log(navigator.geolocation.getCurrentPosition(position => console.log(position)))

// const data = getweatherData('worcester')
// extractData(data)
getDeviceLocationWeatherData();

const form = document.querySelector("form");
const input = form.querySelector("input");
form.addEventListener("submit", async (e) => {
  e.preventDefault();
  const value = input.value.trim();
  if (value) {
    const data = await getweatherData(value);
    console.log({ data });
    const extractedData = await extractData(data);
    console.log({ extractedData });
    renderWeatherData(extractedData);
  }
  console.log("submitted");
});

const mode = document.querySelector(".toggle-degree");
const unit = mode.querySelector(".unit");
const city = document.querySelector(".city");
let data = null;
mode.addEventListener("click", async (e) => {
  if (unit.textContent === "F") {
    unit.textContent = "C";
    data = await getweatherData(city.textContent, "metric");
  } else {
    unit.textContent = "F";
    data = await getweatherData(city.textContent, "imperial");
  }
  console.log({ data });
  const extractedData = await extractData(data);
  console.log({ extractedData });
  renderWeatherData(extractedData);
});
