export const fetchData = async (url) => {
  try {
    const response = await fetch(url);
    return await response.json();
  } catch (error) {
    console.log(error);
  }
};

export const API_KEY = "d1fccaa201f0a7001530f5c954ecd382";

export const getweatherData = async (input, unit = "imperial") => {
  try {
    let url = null;
    if (Number(input)) {
      url = `https://api.openweathermap.org/data/2.5/weather?zip=${input}&appid=${API_KEY}&units=${unit}`;
    } else {
      url = `https://api.openweathermap.org/data/2.5/weather?q=${input}&appid=${API_KEY}&units=${unit}`;
    }
    return await fetchData(url);
  } catch (error) {
    console.log(error);
  }
};

export const getDeviceLocationWeatherData = () => {
  let url = null;
  let extractedData = null;

  navigator.geolocation.getCurrentPosition(async (position) => {
    let lat = position.coords.latitude;
    let lon = position.coords.longitude;
    url = `https://api.openweathermap.org/data/2.5/weather?lat=${lat}&lon=${lon}&appid=${API_KEY}&units=imperial`;
    console.log({ url });
    const data = await fetchData(url);
    console.log({ data });
    extractedData = await extractData(data);
    console.log({ extractedData });
    renderWeatherData(extractedData);
  });
};

