export const extractData = async (data) => {
  const {
    weather: [{ description, icon }],
    main: { temp, temp_min: minTemp, temp_max: maxTemp, feels_like: feelsLike },
    sys: { country },
    name
  } = await data;

  return {
    description,
    icon,
    temp,
    minTemp,
    maxTemp,
    feelsLike,
    country,
    name,
    imgSrc: `https://openweathermap.org/img/wn/${icon}@2x.png`
  };
};

export const renderWeatherData = ({
  description,
  temp,
  minTemp,
  maxTemp,
  feelsLike,
  country,
  name,
  imgSrc
}) => {
  const days = ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"];
  const display = document.querySelector(".display");
  display.innerHTML = `
  <div class="temp-icon-wrapper">
  <div class="temp">${temp}&#176;</div>
  <img src="${imgSrc}" alt="" />
</div>
<div class="city-wrapper">
  <span class="city">${name}</span>
  <span>- ${country}</span>
</div>
<div class="description">${description}</div>
<div class="min-max-fells-wrapper">
  <span>${maxTemp}&#176; / ${minTemp}&#176; Feels like ${feelsLike}&#176;</span>
</div>
<div class="date">${
    days[new Date().getDay()]
  }, ${new Date().toLocaleTimeString()}</div>
  `;
};
